import React, { Component } from 'react';


class Current extends Component {
    state = {
            projectsObject : [
                { id: 1, url: './assets/images/2.png', name: 'projectname', desc: 'lorem ipsum...', github: 'https://github', server: 'https://server'},
                { id: 2, url: './assets/images/2.png', name: 'projectname1', desc: 'lorem ipsum...1', github: 'https://github1', server: 'https://server1'},
                { id: 3, url: './assets/images/2.png', name: 'projectname1', desc: 'lorem ipsum...1', github: 'https://github1', server: 'https://server1'}
            ]
        };

    render(){
        return(
            <section className="portfolio">  
                <div className="container">
                    <h4 className="h4">I'm currently working</h4>
                    <div className="row">
                    { this.state.projectsObject.map( project => {
                        return(
                            <div className="col s12 m6 l4" key={ project.id }>
                                <div className="card">
                                    <div className="card-image">
                                        <img src={ project.url } alt="project-images" />
                                        <span className="card-title">{ project.name }</span>
                                    </div>
        
                                    <div className="card-content">
                                        <p>{ project.desc }</p>
                                    </div>
        
                                    <div className="card-action">
                                        <a href={ project.github }>Github</a>
                                        <a href={ project.server }>Server</a>
                                    </div>
                                </div>
                            </div>
                        )
                } )

            }

                    </div>
                </div>
            </section>
        )
    }
}
export default Current;