import React, { Component } from 'react';

class Contact extends Component {
    state = {
             F_url: 'https://www.facebook.com/svaridze',
             F_text: 'Facebook',
             F_account: '/svaridze',

            G_url: 'mailto:aleksisvaridze@gmail.com',
            G_text: 'Gmail',    
            G_account: 'aleksisvaridze@gmail.com',

            O_url: 'mailto:aleksisvaridze@outlook.com', 
            O_text: 'Outlook',  
            O_account: 'aleksisvaridze@outlook.com',

            M_url: '',
            M_text: 'Mobile',   
            M_account: '593 53 90 95'
    }
    render() {
        return(
            <section className="contact" id="contact">
                <h1 className="section-heading">Contact</h1>
                <div className="container">
                    <div className="row">
                        <div className="col s12 m12 contact-items">

                            <div className="card">
                                <div className="card-action">
                                    <span className="card-title"><i className="material-icons margin-right">chat</i>{ this.state.F_text }</span>
                                    <a href={ this.state.F_url } target="_blank" rel="noopener noreferrer">{ this.state.F_account }</a>
                                </div>
                            </div>

                            <div className="card">
                                <div className="card-action">
                                    <span className="card-title"><i className="material-icons margin-right">mail</i>{ this.state.G_text }</span>
                                    <a href={ this.state.G_url }>{ this.state.G_account }</a>
                                </div>
                            </div>

                            <div className="card">
                                <div className="card-action">
                                    <span className="card-title"><i className="material-icons margin-right">mail</i>{ this.state.O_text }</span>
                                    <a href={ this.state.O_url }>{ this.state.O_account }</a>
                                </div>
                            </div>

                            <div className="card">
                                <div className="card-action">
                                    <span className="card-title"><i className="material-icons margin-right">call</i>{ this.state.M_text }</span>
                                    <a href="#!">{ this.state.M_account }</a>
                                </div>
                            </div>

                        </div>
                    </div>
                </div>
            </section>
        )
    }
}

export default Contact;