import React from 'react';
import me from '../media/me.jpg';

const About = () => {
    return(
        <section className="container about" id="about">
            <h1 className="section-heading">A little bit about me</h1>
            <div className="row">

                <article className="margin-bottom col l6 m6 s12">
                    <section className="about-me">
                        <div className="me-wrapper">
                            <img src={ me } className="me" alt="me" />
                        </div>
                        <h1>Aleksi Svaridze</h1>
                        <p>Lorem  HTML files are parsed directly at deploy time, so there’s no need for you to make an API call or include extra JavaScript on your site.</p>
                    </section>
                </article>

                <article className="col l6 m6 s12">
                    <section className="about-skills">

                        <div className="skillbar" data-percent="80">
                            <span className="skillbar-title html">Html5</span>
                            {/* <p className="skillbar-bar" style={{background: '#e67e22'}}></p> */}
                            <span className="skill-bar-percent">80%</span>
                        </div>
    
                        <div className="skillbar" data-percent="85">
                            <span className="skillbar-title css">Css3</span>
                            {/* <p className="skillbar-bar" style={{background: '#3498db'}}></p> */}
                            <span className="skill-bar-percent">85%</span>
                        </div>

                        <div className="skillbar" data-percent="50">
                            <span className="skillbar-title sass">Sass</span>
                            {/* <p className="skillbar-bar" style={{background: '#3498db'}}></p> */}
                            <span className="skill-bar-percent">50%</span>
                        </div>

                        <div className="skillbar" data-percent="60">
                            <span className="skillbar-title btst">Bootstrap</span>
                            {/* <p className="skillbar-bar" style={{background: '#3498db'}}></p> */}
                            <span className="skill-bar-percent">60%</span>
                        </div>

                        <div className="skillbar" data-percent="50">
                            <span className="skillbar-title mtrl">Materialize</span>
                            {/* <p className="skillbar-bar" style={{background: '#3498db'}}></p> */}
                            <span className="skill-bar-percent">50%</span>
                        </div>
    
                        <div className="skillbar" data-percent="40">
                            <span className="skillbar-title js">JavaScript</span>
                            {/* <p className="skillbar-bar" style={{background: '#2c3e50'}}></p> */}
                            <span className="skill-bar-percent">40%</span>
                        </div>
    
                        <div className="skillbar" data-percent="40">
                            <span className="skillbar-title jq">jQuery</span>
                            {/* <p className="skillbar-bar" style={{background: '#5a68a5'}}></p> */}
                            <span className="skill-bar-percent">40%</span>
                        </div>

                        <div className="skillbar" data-percent="30">
                            <span className="skillbar-title rj">React.js</span>
                            {/* <p className="skillbar-bar" style={{background: '#3498db'}}></p> */}
                            <span className="skill-bar-percent">30%</span>
                        </div>

                        <div className="skillbar" data-percent="40">
                            <span className="skillbar-title ps">PhotoShop</span>
                            {/* <p className="skillbar-bar" style={{background: '#e67e22'}}></p> */}
                            <span className="skill-bar-percent">40%</span>
                        </div>

                        <div className="skillbar" data-percent="40">
                            <span className="skillbar-title ai">Illustrator</span>
                            {/* <p className="skillbar-bar" style={{background: '#e67e22'}}></p> */}
                            <span className="skill-bar-percent">40%</span>
                        </div>

                        <div className="skillbar" data-percent="50">
                            <span className="skillbar-title wp">WordPress</span>
                            {/* <p className="skillbar-bar" style={{background: '#e67e22'}}></p> */}
                            <span className="skill-bar-percent">50%</span>
                        </div>

                    </section>
                </article>

            </div>
        </section>
    )
}

export default About;